import React from "react"
import PropTypes from "prop-types"

import SEO from "../components/seo"
import HeroBanner from "../components/HeroBanner.js"
import MenuBar from "../components/MenuBar.js"
import Footer from "../components/Footer.js"

import "../assets/scss/styles.scss";
import "../assets/js/scripts.js";

const ContentPage = ({pageTitle, children}) => (
  <div className="page-wrapper">
    <SEO title = {pageTitle} />
        <MenuBar />
        <HeroBanner 
            bannerType = {"content-page-banner"}
            bannerTitle = {pageTitle}
        />

        {/* Print page title to course name for the css to target */}
        <main className = "">
          <div className = {'cm-content-wrapper pg-' + pageTitle.replace(' ' , '-').toLowerCase()} id = 'main-content'>
            {children}
          </div>
        </main>

        <Footer />
  </div>
)

ContentPage.propTypes = {
  pageTitle: PropTypes.string,
}

ContentPage.defaultProps = {
  pageTitle: "Page Title"
}

export default ContentPage
