import React from "react"

class HeroBanner extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            backgroundNumber: 0
        };
    }

    handleClick(direction) {
        // Max of 6 background - remember to update the css if you want more
        var numBackgrounds = 5;
        var bgNum = this.state.backgroundNumber;

        // Invert if it's outside of bounds for a wrap around effect
        function backgroundNumberValidator(num) {
            num = num < 0               ? num = numBackgrounds : num;
            num = num > numBackgrounds  ? num = 0              : num;
            return num;
        }

        if (direction === "right") {
            bgNum++;
            bgNum = backgroundNumberValidator(bgNum);

            this.setState({
                backgroundNumber: bgNum
            });
        } 
        else if (direction === "left") {
            bgNum--;
            bgNum = backgroundNumberValidator(bgNum);

            this.setState({
                backgroundNumber: bgNum
            });
        }
    }

    render() {
        return (
            <div className={"cm-hero-banner " + this.props.bannerType + " background-" + this.state.backgroundNumber} id="page-banner">
                
                <div className="hero-banner-content">
                    <h1>{this.props.bannerTitle}</h1>
                    {this.props.bannerText ? <p>{this.props.bannerText}</p> : ""}

                    {/* Link is only for homepage full height banners due to height constraints */}
                    {
                        this.props.bannerType === "home-page-banner" ?  
                            this.props.bannerLinkText && this.props.bannerLinkURL ? 
                                <a href= {this.props.bannerLinkURL} className='cta cta-primary'>{this.props.bannerLinkText}</a> 
                            : ""
                        : '' 
                    }
                </div>

                {/* For Carousel Banners: */}
                {
                    this.props.bannerHasCarousel ?  
                        <div className="hero-banner-carousel-controls">
                            <div className="carousel-control scroll-left"  onClick={() => this.handleClick("left" )}></div>
                            <div className="carousel-control scroll-right" onClick={() => this.handleClick("right")}></div>
                        </div>
                    : '' 
                }

                {/* Animation arrow for homepage full height */}
                {
                    this.props.bannerType === "home-page-banner" ?  
                        <div className="hero-banner-next">
                            <a href="#main-content" className="vsa-block-link">Link to Content</a>
                        </div>
                    : '' 
                }

            </div>
        )
    }
}

