import { Link } from "gatsby"
import React from "react"

class NavLinks extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            menuDisplayed: false
        };
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
       this.setState({
           menuDisplayed: !this.state.menuDisplayed
       })
    }

    render() {
        return (
            <div className="cm-nav-links">
                <div className="mobile-menu-icon" onClick={this.handleClick}></div>
                <div className={"nav-wrapper visible-" + this.state.menuDisplayed}>
                    <ul>
                        <li>
                            <Link to="/registration">
                                Registration
                            </Link>
                        </li>

                        <li>
                            <Link to="/event-details">
                                Event Details
                            </Link>
                        </li>

                        <li>
                            <Link to="/course-details">
                                Course
                            </Link>
                        </li>

                        <li>
                            <Link to="/training">
                                Training
                            </Link>
                        </li>

                        <li>
                            <Link to="/results">
                                Results
                            </Link>
                        </li>

                        <li>
                            <Link to="/faqs">
                                FAQs
                            </Link>
                        </li>

                        <li>
                            <Link to="/contacts">
                                Contacts
                            </Link>
                        </li>
                    </ul>
                </div>
            </div>
        )
    }
}

export default NavLinks;
