import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

import NavLinks from "../components/NavLinks.js"
import SocialLinks from "../components/SocialLinks.js"

class MenuBar extends React.Component {
    handleScroll() {
        var menuBar, pageBanner, changePoint;

        menuBar = document.getElementById("menu-bar");
        pageBanner = document.getElementById("page-banner");
        changePoint = 10;

        if (window.scrollY > changePoint) {
            menuBar.classList.add("scrolled");
        }
        else {
            menuBar.classList.remove("scrolled");
        }
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    render() {
        return (
            <nav className="cm-menu-bar" id="menu-bar">

                <div className="logo">
                    <Link 
                        to="/"
                        className="vsa-block-link">
                        Home
                    </Link>
                </div>

                <NavLinks />

                <SocialLinks />

            </nav>
        )
    }
}

MenuBar.propTypes = {
    navLinkData: PropTypes.object,
    socialLinkData: PropTypes.object,
}

MenuBar.defaultProps = {
    navLinkData: {},
    socialLinkData: {},
}

export default MenuBar;
